import React, { useState } from 'react';
import { 
  Card, 
  Button, 
  Typography, 
  Row, 
  Col, 
  Input, 
  Select, 
  List, 
  Tag, 
  Space,
  Modal,
  Form,
  Upload,
  message
} from 'antd';
import { 
  PlusOutlined, 
  SearchOutlined, 
  FilterOutlined,
  CameraOutlined
} from '@ant-design/icons';

const { Title, Paragraph } = Typography;
const { Option } = Select;
const { TextArea } = Input;

const Requests: React.FC = () => {
  const [isModalVisible, setIsModalVisible] = useState(false);
  const [selectedRequest, setSelectedRequest] = useState<any>(null);
  const [filterType, setFilterType] = useState<string>('all');
  const [searchText, setSearchText] = useState<string>('');

  //   
  const requests = [
    {
      id: 1,
      title: '  LG',
      description: '  ,  .    .',
      type: 'refrigerator',
      district: '',
      budget: 3000,
      status: 'new',
      date: '2024-01-20',
      client: ' '
    },
    {
      id: 2,
      title: '  ',
      description: '      .',
      type: 'washing_machine',
      district: '',
      budget: 1500,
      status: 'in_progress',
      date: '2024-01-19',
      client: ' '
    },
    {
      id: 3,
      title: '  Samsung',
      description: '  ,  .',
      type: 'tv',
      district: '',
      budget: 2500,
      status: 'completed',
      date: '2024-01-18',
      client: ' '
    }
  ];

  const showRequestDetails = (request: any) => {
    setSelectedRequest(request);
    setIsModalVisible(true);
  };

  const handleCreateRequest = () => {
    // TODO:   
    message.success('       ');
  };

  const getStatusTag = (status: string) => {
    const colors: Record<string, string> = {
      'new': 'blue',
      'in_progress': 'orange',
      'completed': 'green',
      'cancelled': 'red'
    };
    const texts: Record<string, string> = {
      'new': '',
      'in_progress': ' ',
      'completed': '',
      'cancelled': ''
    };
    return <Tag color={colors[status]}>{texts[status]}</Tag>;
  };

  const getTypeText = (type: string) => {
    const types: Record<string, string> = {
      'washing_machine': ' ',
      'refrigerator': '',
      'oven': ' ',
      'dishwasher': '',
      'tv': '',
      'computer': '',
      'other': ''
    };
    return types[type] || type;
  };

  const filteredRequests = requests.filter(request => {
    if (filterType !== 'all' && request.type !== filterType) return false;
    if (searchText && !request.title.toLowerCase().includes(searchText.toLowerCase())) return false;
    return true;
  });

  return (
    <div style={{ padding: '20px' }}>
      <Row justify="space-between" align="middle" style={{ marginBottom: '24px' }}>
        <Col>
          <Title level={2}>  </Title>
          <Paragraph type="secondary">
                 
          </Paragraph>
        </Col>
        <Col>
          <Button 
            type="primary" 
            icon={<PlusOutlined />} 
            size="large"
            onClick={handleCreateRequest}
          >
             
          </Button>
        </Col>
      </Row>

      {/*    */}
      <Card style={{ marginBottom: '24px' }}>
        <Row gutter={[16, 16]}>
          <Col xs={24} md={12}>
            <Input
              placeholder="    ..."
              prefix={<SearchOutlined />}
              value={searchText}
              onChange={(e) => setSearchText(e.target.value)}
            />
          </Col>
          <Col xs={24} md={6}>
            <Select
              placeholder=" "
              style={{ width: '100%' }}
              value={filterType}
              onChange={setFilterType}
              suffixIcon={<FilterOutlined />}
            >
              <Option value="all"> </Option>
              <Option value="washing_machine"> </Option>
              <Option value="refrigerator"></Option>
              <Option value="tv"></Option>
              <Option value="computer"></Option>
              <Option value="other"></Option>
            </Select>
          </Col>
          <Col xs={24} md={6}>
            <Select
              placeholder=""
              style={{ width: '100%' }}
              suffixIcon={<FilterOutlined />}
            >
              <Option value="all"> </Option>
              <Option value=""></Option>
              <Option value=""></Option>
              <Option value=""></Option>
              <Option value=""></Option>
            </Select>
          </Col>
        </Row>
      </Card>

      {/*   */}
      <List
        dataSource={filteredRequests}
        renderItem={request => (
          <List.Item>
            <Card 
              hoverable 
              style={{ width: '100%' }}
              onClick={() => showRequestDetails(request)}
            >
              <Row justify="space-between" align="middle">
                <Col xs={24} md={16}>
                  <Title level={4} style={{ marginBottom: '8px' }}>
                    {request.title}
                  </Title>
                  <Paragraph ellipsis={{ rows: 2 }}>
                    {request.description}
                  </Paragraph>
                  <Space style={{ marginTop: '12px' }}>
                    <Tag>{getTypeText(request.type)}</Tag>
                    <Tag>{request.district}</Tag>
                    {getStatusTag(request.status)}
                    {request.budget && <Tag color="green">{request.budget} </Tag>}
                  </Space>
                </Col>
                <Col xs={24} md={8} style={{ textAlign: 'right' }}>
                  <Paragraph type="secondary">
                    {request.date}
                  </Paragraph>
                  <Paragraph>
                    <strong>{request.client}</strong>
                  </Paragraph>
                  <Button type="primary" style={{ marginTop: '12px' }}>
                    
                  </Button>
                </Col>
              </Row>
            </Card>
          </List.Item>
        )}
        locale={{ emptyText: '  ' }}
      />

      {/*     */}
      <Modal
        title=" "
        open={isModalVisible}
        onCancel={() => setIsModalVisible(false)}
        footer={[
          <Button key="close" onClick={() => setIsModalVisible(false)}>
            
          </Button>,
          <Button key="respond" type="primary">
            
          </Button>
        ]}
        width={700}
      >
        {selectedRequest && (
          <div>
            <Title level={4}>{selectedRequest.title}</Title>
            <Paragraph>{selectedRequest.description}</Paragraph>
            
            <Row gutter={[16, 16]} style={{ marginTop: '20px' }}>
              <Col span={12}>
                <strong> :</strong>
                <div>{getTypeText(selectedRequest.type)}</div>
              </Col>
              <Col span={12}>
                <strong>:</strong>
                <div>{selectedRequest.district}</div>
              </Col>
              <Col span={12}>
                <strong>:</strong>
                <div>{selectedRequest.budget} </div>
              </Col>
              <Col span={12}>
                <strong>:</strong>
                <div>{getStatusTag(selectedRequest.status)}</div>
              </Col>
              <Col span={12}>
                <strong> :</strong>
                <div>{selectedRequest.date}</div>
              </Col>
              <Col span={12}>
                <strong>:</strong>
                <div>{selectedRequest.client}</div>
              </Col>
            </Row>
          </div>
        )}
      </Modal>
    </div>
  );
};

export default Requests;