import React, { useState, useEffect } from 'react';
import { 
  Plus, 
  Filter, 
  Search, 
  User, 
  Briefcase, 
  BarChart3,
  Calendar,
  Clock,
  CheckCircle,
  AlertCircle,
  Image as ImageIcon,
  Upload,
  X
} from 'lucide-react';
import RequestCard from '../components/requests/RequestCard';
import './Dashboard.css';

// 
interface UserData {
  id: number;
  name: string;
  phone: string;
  role: 'client' | 'master' | 'admin';
  avatar?: string;
  city?: string;
  rating: number;
  completed_orders: number;
}

interface Request {
  id: number;
  title: string;
  description: string;
  type: string;
  district: string;
  budget?: number;
  status: 'new' | 'searching' | 'in_progress' | 'completed' | 'cancelled';
  clientName: string;
  clientPhone?: string;
  createdAt: string;
  photos?: string[];
}

const Dashboard: React.FC = () => {
  const [user, setUser] = useState<UserData | null>(null);
  const [activeTab, setActiveTab] = useState<'overview' | 'requests' | 'stats'>('overview');
  const [isCreateFormOpen, setIsCreateFormOpen] = useState(false);
  const [loading, setLoading] = useState(true);
  
  //     
  const [newRequest, setNewRequest] = useState({
    title: '',
    description: '',
    type: 'other',
    brand: '',
    model: '',
    address: '',
    district: '',
    budget: '',
    photos: [] as File[]
  });

  //  
  const [clientRequests, setClientRequests] = useState<Request[]>([]);
  
  //    
  const [availableRequests, setAvailableRequests] = useState<Request[]>([]);
  
  //   
  const [filters, setFilters] = useState({
    type: 'all',
    district: 'all',
    minBudget: '',
    maxBudget: ''
  });

  //   
  useEffect(() => {
    const loadUserData = () => {
      const userData = localStorage.getItem('masterok_user');
      if (userData) {
        try {
          const parsedUser = JSON.parse(userData);
          setUser(parsedUser);
          
          //   
          if (parsedUser.role === 'client') {
            loadClientRequests(parsedUser.id);
          } else if (parsedUser.role === 'master') {
            loadAvailableRequests();
          }
        } catch (error) {
          console.error('Error parsing user data:', error);
        }
      }
      setLoading(false);
    };

    loadUserData();
  }, []);

  //   
  const loadClientRequests = (userId: number) => {
    //    
    const mockRequests: Request[] = [
      {
        id: 1,
        title: '  LG',
        description: '  ,   .    .',
        type: 'refrigerator',
        district: '',
        budget: 3000,
        status: 'in_progress',
        clientName: ' ',
        clientPhone: '+79131112233',
        createdAt: '2024-01-20T10:30:00'
      },
      {
        id: 2,
        title: ' ',
        description: '      20 ..',
        type: 'other',
        district: '',
        budget: 5000,
        status: 'new',
        clientName: ' ',
        clientPhone: '+79131112233',
        createdAt: '2024-01-19T14:20:00'
      },
      {
        id: 3,
        title: '  ',
        description: '     .  .',
        type: 'dishwasher',
        district: '',
        budget: 2500,
        status: 'completed',
        clientName: ' ',
        clientPhone: '+79131112233',
        createdAt: '2024-01-18T09:15:00'
      }
    ];
    setClientRequests(mockRequests);
  };

  //     
  const loadAvailableRequests = () => {
    //    
    const mockRequests: Request[] = [
      {
        id: 4,
        title: '  ',
        description: '  ,   .  Indesit IWSC 5105.',
        type: 'washing_machine',
        district: '',
        budget: 2800,
        status: 'new',
        clientName: ' ',
        clientPhone: '+79132223344',
        createdAt: '2024-01-20T11:00:00'
      },
      {
        id: 5,
        title: '  ',
        description: '     Bosch.',
        type: 'oven',
        district: '',
        budget: 1800,
        status: 'new',
        clientName: ' ',
        clientPhone: '+79133334455',
        createdAt: '2024-01-20T10:15:00'
      },
      {
        id: 6,
        title: ' ',
        description: '  .   .  .',
        type: 'computer',
        district: '',
        budget: 1500,
        status: 'new',
        clientName: ' ',
        clientPhone: '+79134445566',
        createdAt: '2024-01-19T16:30:00'
      }
    ];
    setAvailableRequests(mockRequests);
  };

  //   
  const handleCreateRequest = async (e: React.FormEvent) => {
    e.preventDefault();
    
    // 
    if (!newRequest.title.trim() || !newRequest.description.trim()) {
      alert('  ');
      return;
    }

    try {
      //     API
      console.log(' :', newRequest);
      
      //  
      alert('  !');
      
      //    
      setNewRequest({
        title: '',
        description: '',
        type: 'other',
        brand: '',
        model: '',
        address: '',
        district: '',
        budget: '',
        photos: []
      });
      setIsCreateFormOpen(false);
      
      //   
      if (user?.role === 'client') {
        loadClientRequests(user.id);
      }
    } catch (error) {
      console.error('  :', error);
      alert('   ');
    }
  };

  //     
  const handleRespondToRequest = async (requestId: number) => {
    try {
      //     API
      console.log('  :', requestId);
      
      alert('  !');
      
      //    
      setAvailableRequests(prev => 
        prev.filter(req => req.id !== requestId)
      );
    } catch (error) {
      console.error(' :', error);
      alert('   ');
    }
  };

  //   
  const handlePhotoUpload = (e: React.ChangeEvent<HTMLInputElement>) => {
    const files = e.target.files;
    if (files) {
      const newPhotos = Array.from(files).slice(0, 5); //  5 
      setNewRequest(prev => ({
        ...prev,
        photos: [...prev.photos, ...newPhotos]
      }));
    }
  };

  //  
  const handleRemovePhoto = (index: number) => {
    setNewRequest(prev => ({
      ...prev,
      photos: prev.photos.filter((_, i) => i !== index)
    }));
  };

  //    
  const filteredRequests = availableRequests.filter(request => {
    if (filters.type !== 'all' && request.type !== filters.type) return false;
    if (filters.district !== 'all' && request.district !== filters.district) return false;
    if (filters.minBudget && (request.budget || 0) < Number(filters.minBudget)) return false;
    if (filters.maxBudget && (request.budget || 0) > Number(filters.maxBudget)) return false;
    return true;
  });

  if (loading) {
    return (
      <div className="dashboard-loading">
        <div className="loading-spinner"></div>
        <p> ...</p>
      </div>
    );
  }

  if (!user) {
    return (
      <div className="dashboard-error">
        <AlertCircle size={48} />
        <h3>  </h3>
        <p>,   </p>
      </div>
    );
  }

  return (
    <div className="dashboard">
      {/*   */}
      <div className="dashboard-header">
        <div className="user-info">
          <div className="user-avatar">
            {user.avatar ? (
              <img src={user.avatar} alt={user.name} />
            ) : (
              <User size={32} />
            )}
          </div>
          <div className="user-details">
            <h1> , {user.name}!</h1>
            <div className="user-stats">
              <span className="user-role">
                <Briefcase size={16} />
                {user.role === 'client' ? '' : ''}
              </span>
              <span className="user-rating">
                 {user.rating.toFixed(1)}
              </span>
              <span className="user-orders">
                <CheckCircle size={16} />
                {user.completed_orders} 
              </span>
            </div>
          </div>
        </div>

        {/*       */}
        {user.role === 'client' && (
          <button 
            className="create-request-btn"
            onClick={() => setIsCreateFormOpen(true)}
          >
            <Plus size={20} />
            <span> </span>
          </button>
        )}
      </div>

      {/*   */}
      <div className="dashboard-content">
        {/*  */}
        <div className="dashboard-nav">
          <button 
            className={`nav-btn ${activeTab === 'overview' ? 'active' : ''}`}
            onClick={() => setActiveTab('overview')}
          >
            <BarChart3 size={20} />
            <span></span>
          </button>
          <button 
            className={`nav-btn ${activeTab === 'requests' ? 'active' : ''}`}
            onClick={() => setActiveTab('requests')}
          >
            <Briefcase size={20} />
            <span></span>
          </button>
          <button 
            className={`nav-btn ${activeTab === 'stats' ? 'active' : ''}`}
            onClick={() => setActiveTab('stats')}
          >
            <BarChart3 size={20} />
            <span></span>
          </button>
        </div>

        {/*      */}
        {user.role === 'client' ? (
          //  
          <div className="client-dashboard">
            {activeTab === 'overview' && (
              <div className="overview-section">
                <div className="stats-cards">
                  <div className="stat-card">
                    <div className="stat-icon active">
                      <AlertCircle size={24} />
                    </div>
                    <div className="stat-info">
                      <h3></h3>
                      <p>{clientRequests.filter(r => r.status === 'new' || r.status === 'in_progress').length} </p>
                    </div>
                  </div>
                  <div className="stat-card">
                    <div className="stat-icon completed">
                      <CheckCircle size={24} />
                    </div>
                    <div className="stat-info">
                      <h3></h3>
                      <p>{clientRequests.filter(r => r.status === 'completed').length} </p>
                    </div>
                  </div>
                  <div className="stat-card">
                    <div className="stat-icon pending">
                      <Clock size={24} />
                    </div>
                    <div className="stat-info">
                      <h3> </h3>
                      <p>{clientRequests.filter(r => r.status === 'in_progress').length} </p>
                    </div>
                  </div>
                </div>

                <div className="recent-requests">
                  <h2> </h2>
                  <div className="requests-list">
                    {clientRequests.slice(0, 3).map(request => (
                      <RequestCard
                        key={request.id}
                        id={request.id}
                        title={request.title}
                        description={request.description}
                        type={request.type}
                        district={request.district}
                        budget={request.budget}
                        status={request.status}
                        clientName={request.clientName}
                        clientPhone={request.clientPhone}
                        createdAt={request.createdAt}
                      />
                    ))}
                  </div>
                </div>
              </div>
            )}

            {activeTab === 'requests' && (
              <div className="requests-section">
                <div className="section-header">
                  <h2> </h2>
                  <div className="section-actions">
                    <div className="search-box">
                      <Search size={20} />
                      <input type="text" placeholder=" ..." />
                    </div>
                    <select className="filter-select">
                      <option value="all"> </option>
                      <option value="new"></option>
                      <option value="in_progress"> </option>
                      <option value="completed"></option>
                    </select>
                  </div>
                </div>
                
                <div className="requests-grid">
                  {clientRequests.length > 0 ? (
                    clientRequests.map(request => (
                      <RequestCard
                        key={request.id}
                        id={request.id}
                        title={request.title}
                        description={request.description}
                        type={request.type}
                        district={request.district}
                        budget={request.budget}
                        status={request.status}
                        clientName={request.clientName}
                        clientPhone={request.clientPhone}
                        createdAt={request.createdAt}
                      />
                    ))
                  ) : (
                    <div className="empty-state">
                      <Briefcase size={48} />
                      <h3>    </h3>
                      <p>    </p>
                      <button 
                        className="create-btn"
                        onClick={() => setIsCreateFormOpen(true)}
                      >
                        <Plus size={20} />
                         
                      </button>
                    </div>
                  )}
                </div>
              </div>
            )}

            {activeTab === 'stats' && (
              <div className="stats-section">
                <h2></h2>
                <div className="stats-grid">
                  <div className="stat-item">
                    <h4> </h4>
                    <p className="stat-number">{clientRequests.length}</p>
                  </div>
                  <div className="stat-item">
                    <h4> </h4>
                    <p className="stat-number">
                      {Math.round(clientRequests.reduce((sum, r) => sum + (r.budget || 0), 0) / clientRequests.length)} 
                    </p>
                  </div>
                  <div className="stat-item">
                    <h4>  </h4>
                    <p className="stat-number">2.5 </p>
                  </div>
                </div>
              </div>
            )}
          </div>
        ) : (
          //  
          <div className="master-dashboard">
            {activeTab === 'overview' && (
              <div className="overview-section">
                <div className="available-requests">
                  <div className="section-header">
                    <h2> </h2>
                    <div className="section-actions">
                      <div className="search-box">
                        <Search size={20} />
                        <input type="text" placeholder=" ..." />
                      </div>
                      <button className="filter-btn">
                        <Filter size={20} />
                        
                      </button>
                    </div>
                  </div>
                  
                  <div className="filters-panel">
                    <div className="filter-group">
                      <label> </label>
                      <select 
                        value={filters.type}
                        onChange={(e) => setFilters({...filters, type: e.target.value})}
                      >
                        <option value="all"> </option>
                        <option value="washing_machine"> </option>
                        <option value="refrigerator"></option>
                        <option value="oven"> </option>
                        <option value="tv"></option>
                        <option value="computer"></option>
                      </select>
                    </div>
                    
                    <div className="filter-group">
                      <label></label>
                      <select 
                        value={filters.district}
                        onChange={(e) => setFilters({...filters, district: e.target.value})}
                      >
                        <option value="all"> </option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                      </select>
                    </div>
                    
                    <div className="filter-group">
                      <label></label>
                      <div className="budget-range">
                        <input 
                          type="number" 
                          placeholder="" 
                          value={filters.minBudget}
                          onChange={(e) => setFilters({...filters, minBudget: e.target.value})}
                        />
                        <span></span>
                        <input 
                          type="number" 
                          placeholder="" 
                          value={filters.maxBudget}
                          onChange={(e) => setFilters({...filters, maxBudget: e.target.value})}
                        />
                      </div>
                    </div>
                  </div>

                  <div className="requests-grid">
                    {filteredRequests.length > 0 ? (
                      filteredRequests.map(request => (
                        <RequestCard
                          key={request.id}
                          id={request.id}
                          title={request.title}
                          description={request.description}
                          type={request.type}
                          district={request.district}
                          budget={request.budget}
                          status={request.status}
                          clientName={request.clientName}
                          clientPhone={request.clientPhone}
                          createdAt={request.createdAt}
                        />
                      ))
                    ) : (
                      <div className="empty-state">
                        <Briefcase size={48} />
                        <h3>  </h3>
                        <p>   </p>
                      </div>
                    )}
                  </div>
                </div>
              </div>
            )}

            {activeTab === 'requests' && (
              <div className="my-responses">
                <h2> </h2>
                <div className="responses-list">
                  {/*      */}
                  <div className="empty-state">
                    <Briefcase size={48} />
                    <h3>    </h3>
                    <p>   </p>
                  </div>
                </div>
              </div>
            )}

            {activeTab === 'stats' && (
              <div className="master-stats">
                <h2> </h2>
                <div className="stats-grid">
                  <div className="stat-item">
                    <h4> </h4>
                    <p className="stat-number">{user.completed_orders}</p>
                  </div>
                  <div className="stat-item">
                    <h4></h4>
                    <p className="stat-number">{user.rating.toFixed(1)} </p>
                  </div>
                  <div className="stat-item">
                    <h4> </h4>
                    <p className="stat-number">15 </p>
                  </div>
                </div>
              </div>
            )}
          </div>
        )}
      </div>

      {/*     */}
      {isCreateFormOpen && user.role === 'client' && (
        <div className="modal-overlay">
          <div className="create-request-modal">
            <div className="modal-header">
              <h2>  </h2>
              <button 
                className="close-btn"
                onClick={() => setIsCreateFormOpen(false)}
              >
                <X size={24} />
              </button>
            </div>

            <form onSubmit={handleCreateRequest} className="create-form">
              <div className="form-grid">
                <div className="form-group">
                  <label> *</label>
                  <input
                    type="text"
                    value={newRequest.title}
                    onChange={(e) => setNewRequest({...newRequest, title: e.target.value})}
                    placeholder=":  "
                    required
                  />
                </div>

                <div className="form-group">
                  <label>  *</label>
                  <select
                    value={newRequest.type}
                    onChange={(e) => setNewRequest({...newRequest, type: e.target.value})}
                    required
                  >
                    <option value="other"> </option>
                    <option value="washing_machine"> </option>
                    <option value="refrigerator"></option>
                    <option value="oven"> </option>
                    <option value="dishwasher"> </option>
                    <option value="tv"></option>
                    <option value="computer">/</option>
                    <option value="other"></option>
                  </select>
                </div>

                <div className="form-group">
                  <label></label>
                  <input
                    type="text"
                    value={newRequest.brand}
                    onChange={(e) => setNewRequest({...newRequest, brand: e.target.value})}
                    placeholder=": LG, Samsung"
                  />
                </div>

                <div className="form-group">
                  <label></label>
                  <input
                    type="text"
                    value={newRequest.model}
                    onChange={(e) => setNewRequest({...newRequest, model: e.target.value})}
                    placeholder=": GA-B459SLQR"
                  />
                </div>

                <div className="form-group full-width">
                  <label>  *</label>
                  <textarea
                    value={newRequest.description}
                    onChange={(e) => setNewRequest({...newRequest, description: e.target.value})}
                    placeholder="  ..."
                    rows={4}
                    required
                  />
                </div>

                <div className="form-group full-width">
                  <label> *</label>
                  <input
                    type="text"
                    value={newRequest.address}
                    onChange={(e) => setNewRequest({...newRequest, address: e.target.value})}
                    placeholder=", , "
                    required
                  />
                </div>

                <div className="form-group">
                  <label> *</label>
                  <select
                    value={newRequest.district}
                    onChange={(e) => setNewRequest({...newRequest, district: e.target.value})}
                    required
                  >
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=" "> </option>
                    <option value=""></option>
                  </select>
                </div>

                <div className="form-group">
                  <label>  ()</label>
                  <input
                    type="number"
                    value={newRequest.budget}
                    onChange={(e) => setNewRequest({...newRequest, budget: e.target.value})}
                    placeholder=": 3000"
                  />
                </div>

                <div className="form-group full-width">
                  <label>  ( 5 )</label>
                  <div className="photo-upload">
                    <label className="upload-area">
                      <Upload size={24} />
                      <span> </span>
                      <input
                        type="file"
                        accept="image/*"
                        multiple
                        onChange={handlePhotoUpload}
                        style={{ display: 'none' }}
                      />
                    </label>
                    
                    {newRequest.photos.length > 0 && (
                      <div className="preview-gallery">
                        {newRequest.photos.map((photo, index) => (
                          <div key={index} className="photo-preview">
                            <img 
                              src={URL.createObjectURL(photo)} 
                              alt={`Preview ${index + 1}`} 
                            />
                            <button 
                              type="button"
                              className="remove-photo"
                              onClick={() => handleRemovePhoto(index)}
                            >
                              <X size={16} />
                            </button>
                          </div>
                        ))}
                      </div>
                    )}
                  </div>
                </div>
              </div>

              <div className="form-actions">
                <button 
                  type="button" 
                  className="cancel-btn"
                  onClick={() => setIsCreateFormOpen(false)}
                >
                  
                </button>
                <button type="submit" className="submit-btn">
                  <Plus size={20} />
                   
                </button>
              </div>
            </form>
          </div>
        </div>
      )}
    </div>
  );
};

export default Dashboard;